<template>
  <span class="comp-progress">
    <div class="comp-progress-full">
      <div class="comp-progress-curr" :style="{ 'width': percentage + '%' }"></div>
    </div>
  </span>
</template>

<script>

export default {
  name: 'compProgress',

  props: {
    full: Number,
    curr: Number
  },

  computed: {
    percentage () {
      return this.curr / this.full * 100
    }
  }
}
</script>

<style lang="less">
.comp-progress {
  font-size: 0;
  text-align: left;

  &-full {
    height: 14px;
    border-radius: 7px;
    background: #eeeeee;
  }

  &-curr {
    display: inline-block;
    height: 100%;
    border-radius: 7px;
    background: #c7092e;
  }
}
</style>
