import axios from 'axios'

// 创建axios实例
const service = axios.create({
  // baseURL: 'http://horse-activity.chebaba.com',
  timeout: 10000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(
  config => {
    return config
  },
  error => {
    Promise.reject(error)
  }
)

export default service
