<template>
  <div class="comp-bargain-detail">
    <div class="comp-bargain-detail-banner"></div>
    <div class="comp-bargain-detail-container">

      <div class="comp-bargain-detail-wrapper comp-bargain-detail-profile">
        <div class="comp-bargain-detail-profile-avatar" :style="{ 'background-image': 'url(' + wx_data.wx_head_img_url + ')' }"></div>
        <div class="comp-bargain-detail-profile-name">{{ wx_data.wx_name }}</div>
        <div class="comp-bargain-detail-profile-slogan">“我正在0元抢这个宝贝，快来帮我助攻”</div>
        <div class="comp-bargain-detail-profile-info">
          <div class="comp-bargain-detail-profile-info-image" :style="{ 'background-image': 'url(' + goods_info.goods_img + ')' }"></div>
          <div class="comp-bargain-detail-profile-info-desc">
            <div>{{ goods_info.coupon_title }}</div>
            <strong>
              <i class="comp-bargain-detail-profile-icon-bargain"></i>
              ¥{{ goods_info.goods_price }}
            </strong>
          </div>
        </div>
      </div>

      <div class="comp-bargain-detail-wrapper comp-bargain-detail-action">
        <div class="comp-bargain-detail-action-timer">
          距结束还剩：<comp-timer :end_time="activity_info.end_time"></comp-timer>
        </div>
        <div class="comp-bargain-detail-action-status">
          <span v-if="cut_info.is_finish">砍价已结束</span>
          <span v-else>砍价进行中</span>
        </div>
        <div class="comp-bargain-detail-action-money">
          已砍：<strong>{{ cut_info.already_cut_num }}</strong>次，还剩：<strong>{{ goods_info.need_cut_num - cut_info.already_cut_num }}</strong>次
        </div>
        <div class="comp-bargain-detail-action-progress">
          <comp-progress
            :full="goods_info.goods_price"
            :curr="cut_info.already_cut_num"></comp-progress>
        </div>
        <div class="comp-bargain-detail-action-btn">
          <button class="primate-btn" @click="submit">帮TA砍一刀</button>
          <button class="primate-btn" @click="gotoIndex">这个好玩，我也要砍</button>
        </div>
      </div>

      <div class="comp-bargain-detail-wrapper comp-bargain-detail-ranking">
        <div class="comp-bargain-detail-ranking-title">
          <span>砍价榜</span>
        </div>
        <div class="comp-bargain-detail-ranking-list" v-if="cut_ranking.length">
          <div class="comp-bargain-detail-ranking-item"
            v-for="(user, index) in cut_ranking"
            :key="index">
            <div class="comp-bargain-detail-ranking-item-avatar" :style="{ 'background-image': 'url(' + user.wx_head_img_url + ')' }"></div>
            <div class="comp-bargain-detail-ranking-item-name">
              <div>{{ user.wx_name }}</div>
              <span>{{ user.time || Date.now() | formatDate }}</span>
            </div>
            <div class="comp-bargain-detail-ranking-item-money">{{ user.money || 0 }}元</div>
          </div>
        </div>
        <div class="comp-bargain-detail-ranking-empty" v-else>暂无数据</div>
      </div>

    </div>
  </div>
</template>

<script>
import compTimer from '@/components/timer.vue'
import compProgress from '@/components/progress.vue'
import global_ from '@/utils/global'
export default {
  name: 'compBargainDetail',

  components: {
    compTimer,
    compProgress
  },

  props: {
    cut_id: '',
    seller_id: '',
    wx_data: {
      type: Object,
      default () {
        return {}
      }
    }
  },

  filters:{
    formatDate (val) {
      function padDate (v) {
        return v >= 10 ? v : `0${v}`
      }

      let value = new Date(val)
      // let year = value.getFullYear()
      let month = padDate(value.getMonth() + 1)
      let day = padDate(value.getDate())
      let hour = padDate(value.getHours())
      let minutes = padDate(value.getMinutes())
      let seconds = padDate(value.getSeconds())

      return `${month}/${day} ${hour}:${minutes}:${seconds}`
    }
  },

  data () {
    return {
      activity_info: {},
      goods_info: {},
      cut_info: {},
      cut_ranking: []
    }
  },

  methods: {
    getCutInfo () {
      this.$store.dispatch('product/getCutInfo', this.cut_id)
        .then(res => {
          if (typeof res.activity_info === 'object') {
            this.activity_info = res.activity_info
          }
          if (typeof res.goods_info === 'object') {
            this.goods_info = res.goods_info
          }
          if (typeof res.cut_info === 'object') {
            this.cut_info = res.cut_info
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    getCutVisitor () {
      this.$store.dispatch('product/getCutVisitor', this.cut_id)
        .then(res => {
          this.cut_ranking = res
        })
        .catch(err => {
          console.log(err)
        })
    },

    submit () {
      this.$store.dispatch('product/cut', {
        cut_id: this.cut_id,
        wx_openid: this.wx_data.wx_openid,
        wx_name: this.wx_data.wx_name,
        wx_head_img_url: this.wx_data.wx_head_img_url,
        seller_id: this.seller_id
      })
        .then(res => {
          try {
            if (res.code) {
              console.log(res)
            } else {
              alert('砍价成功！');
              window.location.reload();
            }
          } catch (ex) {
              alert(5);
            console.log(ex)
          }
        })
        .catch(err => {
            alert('砍价失败，您已砍价价或该砍价已完成!');
          console.log(err)
        })
    },

    gotoIndex () {
      window.location.href = global_.H5_HOST + '/kanjia.html' + window.location.search
    }
  },

  created () {
    this.getCutInfo()

    this.getCutVisitor()
  }
}
</script>

<style lang="less">
.comp-bargain-detail {
  padding-bottom: 55px;
  background: #eeeded;
  overflow-y: auto;

  &-banner {
    padding-top: 56.8%;
    background: url('./images/banner.png') 50% 50% / cover no-repeat;
  }

  &-container {
    padding: 0 28px;
    box-sizing: border-box;
  }

  &-wrapper {
    padding: 36px 30px;
    box-sizing: border-box;
    margin-top: 30px;
    border-radius: 8px;
    background: #ffffff;
  }

  &-profile {
    position: relative;
    padding-top: 80px;
    margin-top: -160px;

    &-avatar {
      position: absolute;
      top: -68px;
      left: 50%;
      margin-left: -68px;
      width: 134px;
      height: 134px;
      border: 2px solid #ffffff;
      border-radius: 50%;
      background: #aaaaaa 50% 50% / cover no-repeat;
    }

    &-name {
      text-align: center;
      font-size: 28px;
      color: #2d2d2d;
    }

    &-slogan {
      margin-top: 24px;
      padding: 16px 36px;
      box-sizing: border-box;
      background: #eeeded;
      text-align: center;
      font-size: 30px;
      color: #c7092e;
      line-height: 1.2;
      border-radius: 36px;
    }

    &-info {
      display: flex;
      margin-top: 40px;
      padding: 0 48px;
      box-sizing: border-box;

      &-image {
        width: 164px;
        height: 164px;
        border-radius: 8px;
        background: #aaaaaa 50% 50% / cover no-repeat;
      }

      &-desc {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-left: 34px;
        font-size: 30px;
        color: #2d2d2d;
        line-height: 1.2;

        strong {
          margin-top: 30px;
          font-size: 24px;
          color: #c7092e;
          font-weight: 700;
        }
      }
    }
  }

  &-action {
    padding: 36px 110px 20px;
    text-align: center;
    font-size: 26px;
    color: #2d2d2d;

    &-status {
      margin-top: 65px;
      border-top: 1px solid #e3e3e3;

      span {
        display: inline-block;
        position: relative;
        top: -25px;
        padding: 0 26px;
        font-size: 34px;
        background: #ffffff;
      }
    }

    &-money {
      margin-top: 30px;

      strong {
        color: #c7092e;
      }
    }

    &-progress {
      margin-top: 26px;
    }

    &-btn {
      margin-top: 20px;

      button {
        border-radius: 36px;
      }

      button:not(:last-child) {
        background: #f5aa33;
        margin-bottom: 20px;
      }
    }
  }

  &-ranking {
    padding-top: 60px;
    font-size: 26px;
    color: #000000;

    &-title {
      margin: 0 80px;
      box-sizing: border-box;
      text-align: center;
      border-top: 1px solid #e3e3e3;

      span {
        display: inline-block;
        position: relative;
        top: -25px;
        padding: 0 26px;
        font-size: 34px;
        background: #ffffff;
      }
    }

    &-item {
      display: flex;
      align-items: center;
      padding: 30px 0;
      box-sizing: border-box;
      border-bottom: 1px solid #d4d4d4;

      &-avatar {
        margin-right: 30px;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background: #aaaaaa 50% 50% / cover no-repeat;
      }

      &-name {
        flex: 1;
        overflow: hidden;
        line-height: 1.5;

        span {
          color: #d5d5d5;
        }
      }
    }

    &-empty {
      text-align: center;
      padding: 100px 0;
      box-sizing: border-box;
      font-size: 30px;
      color: #e3e3e3;
    }
  }

}
</style>
