import axios from '@/utils/axios'
import utils from '@/utils/index'
import global_ from '@/utils/global'

// const BASE_URL = 'http://horse-activity.chebaba.com'

const user = {
  namespaced: true,

  state: {
    userid: utils.getStorage('userid'),
    token: utils.getStorage('token')
  },

  mutations: {
    SET_USERID (state, userid) {
      state.userid = userid
      utils.setStorage('userid', userid)
    },

    SET_TOKEN (state, token) {
      state.token = token
      utils.setStorage('token', token)
    }
  },

  actions: {
    adduser ({ commit }, args = {}) {
      return new Promise((resolve, reject) => {
        axios({
          url:global_.API_HOST + global_.API_PREFIX + `/api_adduser`,
          method: 'get',
          params: args
        })
          .then(res => {
            if (res.data && res.data.code === 0 && res.data.data && res.data.data.token) {
              commit('SET_USERID', res.data.data.userid)
              commit('SET_TOKEN', res.data.data.token)
              resolve(res.data.data)
            } else {
              reject(res)
            }
          })
          .catch(err => reject(err))
      })
    },

    sendPhoneCode (context, mobile) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/thirdApi_sendSmsCode?mobile=${mobile}`,
          method: 'get'
        })
          .then(res => {
            if (res.data && res.data.code === 0) {
              resolve(res.data)
            } else {
              reject(res)
            }
          })
          .catch(err => reject(err))
      })
    }
  }
}

export default user
