import axios from '@/utils/axios'
import global_ from '@/utils/global'

// const BASE_URL = 'http://horse-activity.chebaba.com'

const wx = {
  namespaced: true,

  state: {
    share_info: {}
  },

  mutations: {
    SET_SHARE_INFO (state, info) {
      state.share_info = info
    }
  },

  actions: {
    getWxShareInfo ({ commit }, referer) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getShareInfo?http_referer=${referer}`,
          method: 'get'
        })
          .then(res => {
            if (res.data) {
              commit('SET_SHARE_INFO', res.data)
              resolve(res.data)
            } else {
              reject(res)
            }
          })
          .catch(err => reject(err))
      })
    }
  }
}

export default wx
