<template>
  <span class="comp-timer">
    <span>{{ hour }}</span> : <span>{{ minute }}</span> : <span>{{ second }}</span>
  </span>
</template>

<script>

export default {
  name: 'compTimer',

  props: {
    end_time: Number
  },

  data () {
    return {
      rest_time: 0,
      countdown_timer: null,

      hour: '00',
      minute: '00',
      second: '00'
    }
  },

  watch: {
    end_time () {
      this.setRestTime()
    }
  },

  methods: {
    countdown () {
      this.countdown_timer = setInterval(() => {
        this.rest_time -= 1
        if (this.rest_time <= 0) {
          clearInterval(this.countdown_timer)
        } else {
          this.update()
        }
      }, 1000)
    },

    setRestTime () {
      let rest_time = this.end_time - Date.now() / 1000 >> 0
      this.rest_time = rest_time > 0 ? rest_time : 0
    },

    update () {
      function padDate (v) {
        return v >= 10 ? v : `0${v}`
      }

      // 活动剩余时间，单位秒
      let rest_time = this.rest_time

      let hour = rest_time / 3600 >> 0
      let minute = rest_time % 3600 / 60 >> 0
      let second = rest_time % 3600 % 60 >> 0

      this.hour = padDate(hour)
      this.minute = padDate(minute)
      this.second = padDate(second)
    }
  },

  mounted () {
    if (this.end_time) {
      this.setRestTime()
    }
    
    this.countdown()
  }
}
</script>

<style lang="less">
.comp-timer span{
  display: inline-block;
  padding: 8px;
  line-height: 1;
  font-size: 22px;
  color: #fff;
  background: #2d2b2d;
  border-radius: 4px;
}
</style>
