import axios from '@/utils/axios'
import global_ from '@/utils/global'

// const BASE_URL = 'http://horse-activity.chebaba.com'

const product = {
  namespaced: true,

  state: {
    carSeries: [], // 车系列表
    sellerGoods: [] // 活动商品列表
  },

  mutations: {
    SET_CAR_SERIES (state, series) {
      state.carSeries = series
    },

    SET_SELLER_GOODS (state, goods) {
      state.sellerGoods = goods
    }
  },

  actions: {
    getCarSeries ({ commit }) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getSeries`,
          method: 'get'
        })
          .then(res => {
            if (res.data && res.data.code === 0 && Array.isArray(res.data.data)) {
              commit('SET_CAR_SERIES', res.data.data)
              resolve(res.data.data)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },

    getSellerGoods ({ commit }, args) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getAllSellerGoods`,
          method: 'get',
          params: args
        })
          .then(res => {
            if (res.data && res.data.code === 0 && Array.isArray(res.data.data)) {
              commit('SET_SELLER_GOODS', res.data.data)
              resolve(res.data.data)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },

    addGoodsToCut (context, args) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getaddGoodsToCut`,
          method: 'get',
          params: args
        })
          .then(res => {
            if (res.data && res.data.code === 0 && res.data.data && res.data.data.cut_id) {
              resolve(res.data.data.cut_id)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },

    getCutInfo (context, cutId) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getCutInfo?cut_id=${cutId}`,
          method: 'get'
        })
          .then(res => {
            if (res.data && res.data.code === 0 && res.data.data) {
              resolve(res.data.data)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },

    getCutVisitor (context, cutId) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_getCutVisitor?cut_id=${cutId}`,
          method: 'get'
        })
          .then(res => {
            if (res.data && res.data.code === 0 && res.data.data && Array.isArray(res.data.data.visitor_cut_list)) {
              resolve(res.data.data.visitor_cut_list)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },

    cut (context, args) {
      return new Promise((resolve, reject) => {
        axios({
          url: global_.API_HOST + global_.API_PREFIX + `/api_cut`,
          method: 'get',
          params: args
        })
          .then(res => {
            if (res.data && res.data.code === 0) {
              resolve(res.data)
            } else {
              reject(res);
            }
          })
          .catch(err => reject(err))
      })
    },
  }
}

export default product
