<template>
  <div id="app">

    <header-bar class="header-bar"></header-bar>

    <transition name="fade">
      <component
        :is="currentComp"
        :seller_id="seller_id"
        :activity_id="activity_id"
        :cut_id="currentCutId"
        :wx_data="wx_data"></component>
    </transition>

  </div>
</template>

<script>
import headerBar from '@/modules/header-bar/index'
import compBargainDetail from './modules/compBargainDetail/index'
import utils from '@/utils/index'

export default {
  name: 'app',

  components: {
    headerBar,
    compBargainDetail
  },

  data () {
    return {
      currentComp: 'compBargainDetail',
      currentCutId: utils.getParam('cut_id'),
      seller_id: utils.getParam('seller_id'),
      activity_id: utils.getParam('activity_id'),

      wx_data: {
        wx_name: '飞行家',
        wx_openid: 'test',
        wx_head_img_url: 'https://puui.qpic.cn/tv/0/37883357/0'
      }
    }
  },

  mounted () {
    let wx_result = window.wx_result
    if (wx_result && wx_result.openid) {
      this.wx_data.wx_name = wx_result.nickname
      this.wx_data.wx_openid = wx_result.openid
      this.wx_data.wx_head_img_url = wx_result.headimgurl
    }
  }
}
</script>

<style lang="less">
@import '~@/less/public.less';

#app {
  position: relative;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff;
}

.header-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.fade-enter-active {
  animation: fade-in .2s linear;
}
.fade-leave-active {
  animation: fade-in .2s linear reverse;
}
@keyframes fade-in {
  0% {
    transform: scale(1.1, 1.1);
    opacity: 0;
  }
  100% {
    transform: scale(1, 1);
    opacity: 1;
  }
}
</style>
